.cell {
    background-color: black;
    transition-property: background-color;
    transition-duration: 300ms;
    color: white;
}
button.cell {
    border: unset;
    font-size: 7px;
    padding: 0;
    user-select: none;
    cursor: pointer;
}
.alive {
    background-color: white;
    color: black;
}
